



















import { Component, Vue, Prop } from 'vue-property-decorator'
import { roomPlayService } from '@/services/roomplay-service'
import { charactersService } from '@/services/characters-service'
import { CharactersDto } from '../models';

@Component({
  components: {
  }
})
/* eslint-disable */
export default class ListRoom extends Vue {

  @Prop() id?: any;
  private status : number = 0;

  private isLoading: boolean = false;

  private listRoomPlay: any = '';
  private listChaId: any[] = [];
  private chaId: number = 0;

  private charactersDTO: CharactersDto = new CharactersDto();
  
  private mounted() {
    this.getListRoomPlay();
  }

  private getListRoomPlay() {
    roomPlayService.getListByRoomIdAndStatus(this.id, this.status)
    .then((res: any) => {
      this.listRoomPlay = res.data;
      
    })
    .finally(() => {
      this.listRoomPlay.forEach((element: any) => {
        this.listChaId.push(element.chaId);
      });
      this.getRandomCharacters();
      this.getCharactersByChaId();
    })
  }

  private getRandomCharacters() {
    this.chaId = this.listChaId[Math.floor(Math.random() * this.listChaId.length)];
  }

  private getCharactersByChaId() {
    charactersService.getCharactersById(this.chaId)
    .then((res: any) => {
      this.charactersDTO = res.data;
      console.log(this.charactersDTO);
    })
  }

}
